<template>
  <div>
    <v-container fluid>
      <v-row>
        <v-switch
          v-model="data.passwordAuthentication"
          label="Password Authentication"
          @change="togglePasswordAuthentication()"
          class="mx-3"
        ></v-switch>

        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon color="primary" v-bind="attrs" v-on="on">
              mdi-information
            </v-icon>
          </template>
          <span>
            Allow password authentication. We recommend this is
            disabled in favour of key authentication.</span
          >
        </v-tooltip>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import api from "../services/api";

export default {
  props: {
    serverId: null,
    passwordAuthentication: null,
  },

  watch: {
    passwordAuthentication: function (newVal) {
      this.data.passwordAuthentication = newVal;
    }
  },

  data() {
    return {
      fetching: false,
      data: {
        passwordAuthentication: false,
      },
    };
  },

  created() {
    this.data.passwordAuthentication = this.passwordAuthentication
  },

  methods: {
    togglePasswordAuthentication() {
      var self = this;
      this.error = "";
      this.fetching = true;
      this.loading = true;

      api
        .post("servers/" + this.serverId + "/password_authentication", {
          save: 1,
          enable: this.data.passwordAuthentication,
        })
        .then(response => {
            console.log(response);

            if(response.data.error) {
              self.error=response.data.error;
            } else if(response.data.success) {
              self.$emit("complete");
            }
          })
        .catch(error => {
            console.log(error);
          })
        .finally(() => {
            self.fetching=false;
            self.loading=false;
          });
    },
  },
};
</script>